:root {
  --fs-column-count: 3;
  --fs-row-width: calc(100vw - 40px);
}
@media (min-width:740px) {
  :root {
    --fs-column-count: 6;
    --fs-row-width: 720px;
  }
}
@media (min-width:980px) {
  :root {
    --fs-column-count: 12;
    --fs-row-width: 960px;
  }
}
@media (min-width:1220px) {
  :root {
    --fs-row-width: 1200px;
  }
}
@media (min-width:1394px) {
  :root {
    --fs-row-width: 1374px;
  }
