html {
  background-color: #fff;
  color: #393939;
}
@supports (scroll-behavior: smooth) {
  html {
    scroll-behavior: smooth;
  }
}

body {
  font-family: "Montserrat", "montserrat", "Source Sans", "Helvetica Neue", Helvetica, "Arial Black", Arial, sans-serif;
  min-width: 320px;
  overflow-x: hidden;
}
body.fs-page-lock {
  overflow-y: hidden;
}

::selection {
  background-color: #e8f9fa;
  text-shadow: none;
}

iframe {
  border: none;
}

menu {
  margin: 0;
  padding: 0;
}

:where(audio, canvas, iframe, img, svg, video) {
  vertical-align: middle;
}

ol:not(.typography ol,
.typography ul):not(.wp-site-blocks ol,
.wp-site-blocks ul),
ul:not(.typography ol,
.typography ul):not(.wp-site-blocks ol,
.wp-site-blocks ul) {
  list-style: none;
  margin-block: 0;
  padding-left: 0;
}

figure {
  margin: 0;
}

hr {
  height: 1px;
  background-color: #cccccc;
  border: none;
  margin: 0;
}

a {
  text-decoration: none;
}

strong {
  font-weight: 700;
}

/* ==========================================================================
	Header
============================================================================= */
.header {
  --background-color: #1e1e64;
  --color: #fff;
  --navigation-color: #fff;
  --navigation-hover-color: #b2ebee;
  --ribbon-border-color: rgba(255, 255, 255, 0.2);
  --search-background-color: rgba(232, 249, 250, 0.15);
  --search-submit-color: #fff;
  --search-submit-hover-color: #b2ebee;
  --breadcrumb-accent-color: #b2ebee;
  --page-title-color: #fff;
  --page-intro-padding-bottom: 95px;
  --header-curve-position: 0;
  position: relative;
  z-index: 10;
}
@media (min-width:980px) {
  .header {
    --page-intro-padding-bottom: 136px;
  }
}
.header.theme_turquoise {
  --background-color: #e8f9fa;
  --color: #000;
  --navigation-color: #000;
  --navigation-hover-color: #1e1e64;
  --ribbon-border-color: rgba(0, 0, 0, 0.1);
  --search-background-color: rgba(255, 255, 255, 0.45);
  --search-submit-color: #00a5ad;
  --search-submit-hover-color: #1e1e64;
  --breadcrumb-accent-color: #1e1e64;
  --page-title-color: #1e1e64;
}
.header.theme_gray {
  --background-color: #f4f4f4;
  --color: #000;
  --navigation-color: #000;
  --navigation-hover-color: #1e1e64;
  --ribbon-border-color: rgba(0, 0, 0, 0.1);
  --search-background-color: rgba(255, 255, 255, 0.45);
  --search-submit-color: #00a5ad;
  --search-submit-hover-color: #1e1e64;
  --breadcrumb-accent-color: #1e1e64;
  --page-title-color: #1e1e64;
}
.header.has_content_image {
  --page-intro-padding-bottom: 40px;
  --header-curve-position: 30px;
}
@media (min-width:740px) {
  .header.has_content_image {
    --header-curve-position: 60px;
  }
}
@media (min-width:980px) {
  .header.has_content_image {
    --header-curve-position: 220px;
  }
}
.body_layout_home .header {
  --header-curve-position: 60px;
}
@media (min-width:740px) {
  .body_layout_home .header {
    --header-curve-position: 200px;
  }
}
@media (min-width:980px) {
  .body_layout_home .header {
    --header-curve-position: 300px;
  }
}
@media (min-width:1220px) {
  .body_layout_home .header {
    --header-curve-position: 400px;
  }
}
.body_layout_school_home .header {
  --header-curve-position: 280px;
}
@media (min-width:500px) {
  .body_layout_school_home .header {
    --header-curve-position: 250px;
  }
}
@media (min-width: 600px) {
  .body_layout_school_home .header {
    --header-curve-position: 170px;
  }
}
@media (min-width:740px) {
  .body_layout_school_home .header {
    --header-curve-position: 300px;
  }
}

.header_main {
  position: relative;
  background-color: var(--background-color);
  color: var(--color);
}
.header_main::after {
  width: 100%;
  height: 61px;
  position: absolute;
  bottom: var(--header-curve-position);
  left: 0;
  background-image: url("../images/header_curve_small.png");
  background-position: left bottom;
  background-repeat: no-repeat;
  content: "";
  display: block;
  pointer-events: none;
}
@media (min-width:980px) {
  .header_main::after {
    height: 112px;
    background-image: url("../images/header_curve_large.png");
    background-position: center bottom;
  }
}
@media (min-width: 1440px) {
  .header_main::after {
    height: auto;
    aspect-ratio: 1440/112;
    background-size: cover;
  }
}
.header.has_content_image .header_main::after {
  box-shadow: 0 var(--header-curve-position) 0 #fff;
}
@media (min-width:980px) {
  .header.has_content_image .header_main::after {
    box-shadow: 0 100px 0 #fff, 0 200px 0 #fff, 0 220px 0 #fff;
  }
}
@media (min-width:980px) {
  .header.has_cta .header_main::after {
    height: 67px;
    bottom: 40px;
    background-image: url("../images/header_curve_cta_large.png");
    box-shadow: 0 40px 0 #fff;
  }
}
@media (min-width: 1440px) {
  .header.has_cta .header_main::after {
    height: auto;
    aspect-ratio: 1440/67;
  }
}
.body_layout_school_home .header_main::after {
  height: 108px;
  background-image: url("../images/header_curve_school_small.png");
  background-position: right bottom;
  box-shadow: 0 108px 0 #fff;
}
@media (min-width:740px) {
  .body_layout_school_home .header_main::after {
    box-shadow: 0 100px 0 #fff, 0 200px 0 #fff, 0 300px 0 #fff;
  }
}
@media (min-width:980px) {
  .body_layout_school_home .header_main::after {
    height: 167px;
    bottom: 200px;
    background-image: url("../images/header_curve_school_large.png");
    background-position: left bottom;
    box-shadow: 0 167px 0 #fff, 0 267px 0 #fff;
  }
}
@media (min-width:1220px) {
  .body_layout_school_home .header_main::after {
    bottom: 250px;
    box-shadow: 0 167px 0 #fff, 0 334px 0 #fff;
  }
}
@media (min-width: 1440px) {
  .body_layout_school_home .header_main::after {
    height: auto;
    aspect-ratio: 1440/167;
  }
}

.header_bg_figure {
  width: 100%;
  height: 100%;
  position: absolute;
  inset: 0;
  opacity: 0.1;
}

.header_bg_image {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.header_main_texture {
  width: 100%;
  height: 100%;
  position: absolute;
  inset: 0;
  background-image: url("../images/header_bg_small.png");
  background-position: left top;
  background-repeat: no-repeat;
}
@media (min-width: 448px) and (max-width: 997px), (min-width: 2190px) {
  .header_main_texture {
    background-size: cover;
  }
}
@media (min-width:980px) {
  .header_main_texture {
    background-image: url("../images/header_bg_large.png");
    background-position: center top;
  }
}

.header_main_group {
  position: relative;
}

.header_primary_inner {
  align-items: center;
  display: flex;
  gap: 10px;
  padding-block: 20px;
}
@media (min-width:980px) {
  .header_primary_inner {
    padding-block: 26px 56px;
  }
}

.header_primary_group {
  position: relative;
  top: -8px;
  align-items: stretch;
  display: flex;
  gap: 12px;
}

.header_ribbon {
  display: none;
}
@media (min-width:980px) {
  .header_ribbon {
    border-bottom: 1px solid var(--ribbon-border-color);
    display: block;
  }
}
@media print {
  .header_ribbon {
    display: none;
  }
}

.header_ribbon_inner {
  display: flex;
  gap: 20px;
}

.header_ribbon_group {
  display: flex;
  gap: 10px;
  padding-block: 10px;
}

.header_school_nav {
  margin-right: auto;
}

@media print {
  .header_search {
    display: none;
  }
}

.header_logo {
  margin-right: auto;
}
@media (max-width: 389px) {
  .header_logo .icon {
    width: max(70vw, 200px);
  }
}
@media (min-width: 390px) and (max-width: 979px) {
  .header_logo .icon {
    width: 280px;
  }
}

@media (max-width:979px) {
  .header_secondary_nav {
    display: none;
  }
}

.header_main_decoration {
  width: 1px;
  height: 75px;
  position: absolute;
  bottom: -13px;
  left: calc((100vw - var(--fs-row-width) * 0.9591) / 2 + 6px);
  z-index: 10;
  background-color: #00a5ad;
}
@media (min-width:980px) {
  .header_main_decoration {
    height: 142px;
    bottom: -32px;
    left: 50%;
  }
}
.body_layout_home .header_main_decoration {
  display: none;
}
.header_main_decoration::before, .header_main_decoration::after {
  width: 5px;
  position: absolute;
  left: -2px;
  aspect-ratio: 1;
  background-color: #00a5ad;
  border-radius: 50%;
  content: "";
  display: block;
}
.header_main_decoration::after {
  bottom: 0;
}
@media (max-width:979px) {
  .header.has_content_image .header_main_decoration {
    display: none;
  }
}
@media (min-width:980px) {
  .header.has_cta .header_main_decoration {
    height: 122px;
    bottom: -2px;
    left: calc((100vw - var(--fs-row-width) * 0.9591) / 2 + 6px);
  }
}
.body_layout_news_detail .header_main_decoration {
  display: none;
}
@media (min-width:980px) {
  .body_layout_news_detail .header_main_decoration {
    height: 122px;
    display: block;
    bottom: -40px;
    left: calc((100vw - var(--fs-row-width) * 0.9591) / 2 + 2px);
  }
}
@media (min-width:1220px) {
  .body_layout_news_detail .header_main_decoration {
    height: 200px;
    bottom: -106px;
  }
}
@media (min-width:1394px) {
  .body_layout_news_detail .header_main_decoration {
    height: 250px;
    bottom: -106px;
  }
}

/* Chat button
============================================================================= */
.header_chat_button {
  font-size: 12px;
  font-weight: 700;
  letter-spacing: 0.06em;
  line-height: 1;
  text-transform: uppercase;
  align-items: center;
  border: 1px solid #b2ebee;
  border-radius: 4px;
  color: var(--color);
  display: flex;
  gap: 8px;
  padding-inline: 16px;
  transition: background-color 0.25s, color 0.25s;
}
.header_chat_button:focus-visible {
  background-color: #b2ebee;
  color: #1e1e64;
}
@media (hover: hover) {
  .header_chat_button:hover {
    background-color: #b2ebee;
    color: #1e1e64;
  }
}

.header_chat_button_icon {
  color: #b2ebee;
  transition: color 0.25s;
}
.header_chat_button:focus-visible .header_chat_button_icon {
  color: #1e1e64;
}
@media (hover: hover) {
  .header_chat_button:hover .header_chat_button_icon {
    color: #1e1e64;
  }
}

