/* Menu Close
============================================================================= */
.menu_close {
  display: none;
}
.js .menu_close {
  display: block;
}

.menu_close_toggle {
  position: absolute !important;
  top: 10px;
  right: 10px;
  z-index: 20;
  width: 40px;
  aspect-ratio: 1;
  background-color: #fff;
  border-radius: 4px;
  box-shadow: 4px 4px 0px rgba(0, 0, 0, 0.1);
  color: #b30838;
  transition: background-color 0.25s, box-shadow 0.25s, color 0.25s;
}
