    right: 35px;
  }
}
@media (min-width:1220px) {
  .menu_close_toggle {
    right: 80px;
  }
}

.menu_close_toggle_inner {
  width: 100%;
  height: 100%;
  position: absolute;
  inset: 0;
  align-items: center;
  display: flex;
  gap: 10px;
}

.menu_close_toggle_icon .icon {
  position: absolute;
  top: 50%;
  left: 50%;
  translate: -50% -50%;
}

.menu_close_toggle_label {
  width: 1px;
  height: 1px;
  position: absolute;
  border: 0;
  clip: rect(0 0 0 0);
  margin: -1px;
  overflow: hidden;
  padding: 0;
}
.menu_close_toggle_label.focusable:active, .menu_close_toggle_label.focusable:focus {
  width: auto;
  height: auto;
  position: static;
  clip: auto;
  margin: 0;
  overflow: visible;
}

