/* ==========================================================================
	Pagination
============================================================================= */
.pagination {
  position: relative;
  display: none;
}
.js .pagination {
  display: block;
}
@media print {
  .js .pagination {
    display: none;
  }
}

/* Pagination Nav
============================================================================= */
.pagination_nav {
  width: 100%;
  height: 100%;
  position: absolute;
  inset: 0;
  align-items: center;
  display: flex;
  justify-content: space-between;
}

.pagination_arrow {
  width: 46px;
  height: 46px;
  display: flex;
  position: relative;
  z-index: 2;
  align-items: center;
  border: 1px solid #00a5ad;
  border-radius: 50%;
  color: #1e1e64;
  justify-content: center;
  opacity: 1;
  transition: background-color 0.25s, color 0.25s, opacity 0.25s, border-color 0.25s;
  visibility: visible;
}
.pagination_arrow:focus-visible {
  background: #b2ebee;
  border-color: #b2ebee;
}
@media (hover: hover) {
  .pagination_arrow:hover {
    background: #b2ebee;
    border-color: #b2ebee;
  }
}

.pagination_arrow_disabled {
  border-color: #b2ebee;
  color: #b2ebee;
  pointer-events: none;
}

.pagination_arrow_label {
  width: 1px;
  height: 1px;
  position: absolute;
  border: 0;
  clip: rect(0 0 0 0);
  margin: -1px;
  overflow: hidden;
  padding: 0;
}
.pagination_arrow_label.focusable:active, .pagination_arrow_label.focusable:focus {
  width: auto;
  height: auto;
  position: static;
  clip: auto;
  margin: 0;
  overflow: visible;
}

.pagination_arrow_icon {
  color: currentColor;
}

/* Pagination Form
============================================================================= */
.pagination_form {
  position: relative;
  text-align: center;
}

.pagination_form_inner {
  align-items: center;
  display: flex;
  justify-content: center;
}
@media (max-width:499px) {
  .pagination_form_inner {
    transform: scale(0.9);
  }
}

.pagination_form_label {
  display: none;
  margin-right: 12px;
}
@media (min-width:500px) {
  .pagination_form_label {
    display: block;
  }
}

.pagination_form_select_wrapper {
  position: relative;
  display: inline-block;
}

.pagination_form_select {
  appearance: none;
  background: #f4f4f4;
  border: 1px solid #a9a9a9;
  border-radius: 5px;
  cursor: pointer;
  font-weight: 600;
  line-height: 1;
  padding: 12px 34px 12px 14px;
  -webkit-appearance: none;
}
@media (min-width:740px) {
  .pagination_form_select {
    font-size: 14px;
  }
}

.pagination_form_select_icon {
  position: absolute;
  top: 50%;
  right: 12px;
  color: #00a5ad;
  pointer-events: none;
  translate: 0 -50%;
}

.pagination_form_suffix {
  margin-inline: 12px;
}

.pagination_form_button {
  display: block;
  background: #b2ebee;
  border-radius: 5px;
  color: #1e1e64;
  box-shadow: 4px 4px 0 rgba(0, 0, 0, 0.1);
  font-size: 14px;
  font-weight: 800;
  padding: 14px 30px;
  text-transform: uppercase;
  transition: box-shadow 0.25s;
}
.pagination_form_button:focus-visible {
  box-shadow: 0px 0px 0 rgba(0, 0, 0, 0.1);
}
@media (hover: hover) {
  .pagination_form_button:hover {
    box-shadow: 0px 0px 0 rgba(0, 0, 0, 0.1);
  }
}

