  --icon-color: #00a5ad;
}
@media (hover: hover) {
  .lazy_video .lazy_video_toggle_btn:hover {
    --icon-color: #00a5ad;
  }
}

.lazy_video {
  width: 100%;
  height: 100%;
  position: absolute;
  inset: 0;
  display: none;
}
.js .lazy_video {
  display: block;
}
@media print {
  .js .lazy_video {
    display: none;
  }
}

.lazy_video_toggle_btn {
  position: absolute;
  top: 50%;
  left: 50%;
  translate: -50% -50%;
  width: var(--icon-size);
  align-items: center;
  color: var(--icon-color);
  display: flex;
  justify-content: center;
}
.lazy_video_toggle_btn::before {
  position: absolute;
  top: 2px;
  right: 2px;
  bottom: 2px;
  left: 2px;
  background-color: #fff;
  border-radius: 50%;
  content: "";
  display: block;
}
.lazy_video_toggle_btn .icon {
  width: 100%;
  position: relative;
  transition: 0.25s ease;
}

.lazy_video_iframe_wrap {
  aspect-ratio: 16/9;
  transition: 0.25s opacity ease, 0ms visibility 0.25s;
  display: block;
  opacity: 0;
  visibility: hidden;
}
@media print {
  .lazy_video_iframe_wrap {
    display: none;
  }
}
.lazy_video.ready .lazy_video_iframe_wrap {
  opacity: 1;
  visibility: visible;
  transition: 0.25s opacity ease;
}
.lazy_video_iframe_wrap iframe {
  width: 100%;
  height: 100%;
  position: absolute;
  inset: 0;
}

.lazy_video_iframe_target {
  height: 100%;
}

