/* ==========================================================================
	Accessibility
============================================================================= */
.clearfix:after {
  clear: both;
  content: "";
  display: table;
}

.visually_hide {
  width: 1px;
  height: 1px;
  position: absolute;
  border: 0;
  clip: rect(0 0 0 0);
  margin: -1px;
  overflow: hidden;
  padding: 0;
}
.visually_hide.focusable:active, .visually_hide.focusable:focus {
  width: auto;
  height: auto;
  position: static;
  clip: auto;
  margin: 0;
  overflow: visible;
}

